@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-neutral-50 text-neutral-800;
  }
  h1 {
    @apply text-3xl font-bold text-primary-800;
  }
  h2 {
    @apply text-2xl font-semibold text-primary-700;
  }
  h3 {
    @apply text-xl font-semibold text-primary-600;
  }
}

@layer components {
  .btn {
    @apply px-4 py-2 rounded-md transition-colors duration-200 font-medium;
  }
  .btn-primary {
    @apply bg-primary-600 text-white hover:bg-primary-700;
  }
  .btn-secondary {
    @apply bg-secondary-600 text-white hover:bg-secondary-700;
  }
  .btn-outline {
    @apply border border-primary-600 text-primary-600 hover:bg-primary-50;
  }
  .card {
    @apply bg-white p-6 rounded-lg shadow-md;
  }
  .input {
    @apply px-4 py-2 border border-neutral-300 rounded-md focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500;
  }

  /* Animation delay classes */
  .animation-delay-200 {
    animation-delay: 0.2s;
  }
  .animation-delay-400 {
    animation-delay: 0.4s;
  }
}
